<template>
  <div class="container my-4">
    <h2 class="text-center mb-4">Vista Combinada de Tareas</h2>

    <!-- Sección: Agregar nueva tarea -->
    <div class="mb-4">
      <h3 class="mb-3">Agregar Nueva Tarea</h3>
      <form @submit.prevent="addTask" class="d-flex gap-2">
        <input 
          type="text" 
          v-model="newTask" 
          class="form-control" 
          placeholder="Ingrese una tarea" 
          required 
        />
        <button type="submit" class="btn btn-primary">Agregar</button>
      </form>
    </div>

    <!-- Sección: Tareas desde la API -->
    <div class="mb-4">
      <h3 class="mb-3">Tareas desde la API</h3>
      <ul class="list-group">
        <template v-if="apiTasks.length === 0">
          <li class="list-group-item text-muted text-center">
            No hay tareas disponibles.
          </li>
        </template>

        <!-- Mostrar las tareas -->
        <template v-else>
          <li
            v-for="task in apiTasks"
            :key="task.id"
            class="list-group-item d-flex justify-content-between align-items-center"
            :class="{ 'bg-success text-white': task.completed }" 
          >
            <div>
              <input 
                type="checkbox" 
                v-model="task.completed" 
                @change="toggleCompletion(task)" 
                :checked="task.completed" 
              />
              {{ task.todo }}
            </div>
            <button class="btn btn-danger btn-sm" @click="removeTask(task)">Eliminar</button>
          </li>
        </template>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      apiTasks: [], // Tareas extraídas de la API
      newTask: '',  // Nueva tarea agregada manualmente
    };
  },
  mounted() {
    this.fetchTasks(); // Llama a la API al montar el componente
  },
  methods: {
    async fetchTasks() {
      try {
        const response = await fetch('https://dummyjson.com/todos'); // API de tareas
        const data = await response.json();
        this.apiTasks = data.todos; // Asigna las tareas desde la API
      } catch (error) {
        console.error('Error al cargar las tareas desde la API:', error);
      }
    },
    addTask() {
      if (!this.newTask.trim()) {
        alert('Por favor, ingrese una tarea válida.');
        return;
      }
      // Agrega la nueva tarea con un id único y estado 'no completada'
      this.apiTasks.push({ id: Date.now(), todo: this.newTask.trim(), completed: false });
      this.newTask = ''; // Limpia el campo de entrada
    },
    toggleCompletion(task) {
      // Cambia el estado de completado de la tarea
      task.completed = !task.completed;
    },
    removeTask(task) {
      // Elimina la tarea de la lista
      this.apiTasks = this.apiTasks.filter(t => t.id !== task.id);
    }
  },
};
</script>

<style scoped>
/* Estilo opcional para el fondo de tarea completada */
.bg-success {
  background-color: #28a745 !important;
}
.text-white {
  color: white !important;
}
</style>
